import $ from 'jquery'
import layui from 'layui'
import Base from 'src/scripts/common/base'
// delete Base['$get']

export default Object.assign({
    /*配置即服务*/

    // 公共api接口调用
    $api (api, param) {
        return new Promise((resolve, reject) => {
            this.$request({
                type: api.type,
                url: api.url,
                data: JSON.stringify(param),
                contentType: 'application/json',
                dataType: 'json',
                success: function (res) {
                    if(res.Success) {
                        resolve(res.Data)
                    }else {
                        reject(res)
                    }
                },
                error: function (err) {
                    reject(err)
                }
            })
        })
    },

    /*
    * 确认弹窗(是/否)
    * */
    $layMsg (msg,cb) {
        let me = this
        let tipsHtml = `
            <div class="ui-msg-content"><span>${msg}</span></div>
        `
        let dialog = layer.open({
            title: false,
            closeBtn: false,
            type: 1,
            btn: ['确定', '取消'],
            content: tipsHtml,
            btnAlign: 'c',
            success: function (layero, index) {
                $(':focus').blur();
            },
            yes: function (index, layero) {
                cb && cb()
                layer.close(dialog)
            },
            btn2: function (index, layero) {
                layer.msg('操作已取消！')
            }
        })
    },
    /*
    * 获取当前系统时间(yyyy-MM-dd HH:MM:SS)
    * */
    $getNowFormatDate() {
        let date = new Date()
        let seperator1 = "-"
        let seperator2 = ":"
        let month = date.getMonth() + 1
        let strDate = date.getDate()
        let strHour = date.getHours()
        let strMinute = date.getMinutes()
        let strSecond = date.getSeconds()

        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate
        }
        if (strHour >= 0 && strHour <=9) {
            strHour = "0" + strHour
        }
        if (strMinute >= 0 && strMinute <=9) {
            strMinute = "0" + strMinute
        }
        if (strSecond >= 0 && strSecond <=9) {
            strSecond = "0" + strSecond
        }
        let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
            + " " + strHour + seperator2 + strMinute
            + seperator2 + strSecond
        return currentdate
    },

    /*
    *时间戳转化(yyyy-MM-dd HH:MM:SS)
    * */
    $timestampToTime (timestamp) {
        let date = new Date(timestamp) // 时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let Y = date.getFullYear() + '-'
        let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
        let D = date.getDate() < 10 ? '0' + (date.getDate()) + ' ' : date.getDate() + ' '
        let h = date.getHours() < 10 ? '0' + (date.getHours()) + ':' : date.getHours() + ':'
        let m = date.getMinutes() < 10 ? '0' + (date.getMinutes()) + ':' : date.getMinutes() + ':'
        let s = date.getSeconds() < 10 ? '0' + (date.getSeconds()) : date.getSeconds()
        return Y+M+D+h+m+s
    },

    /*
    * 日期转化为时间戳
    * */
    $timestampFormatDate (t) {
        if (t === ''){
            return ''
        }
        let date = new Date(t)
        return date.getTime()
    },

    /*
    * sessionStorage操作
    * */
    $setS (key, data) {
        if (window.sessionStorage) {
            if (typeof data === "object") {

            }
            let _data = (typeof data === "object") ? JSON.stringify(data) : data
            sessionStorage.setItem(key, _data)
        }
    },
    $getS (key) {
        if (window.sessionStorage) {

            return JSON.parse(sessionStorage.getItem(key))
        }
    },
    $clearS (key) {
        if (window.sessionStorage) {
            if (key) {
                sessionStorage.clear(key)
            } else {
                sessionStorage.clear()
            }
        }
    }
}, Base)



