import $ from 'jquery'
import layui from 'layui'
import 'babel-polyfill'
import 'src/styles/index.scss'
import tpl from 'src/tpl'
import script from 'src/scripts'
import Route from 'src/scripts/route'
import Util from 'src/scripts/utils'
import modBase from 'src/scripts/common/moduleBase'

/*全局适屏设置*/
;(function () {
    setTimeout(function () {
        let deviceWidth = document.documentElement.clientWidth
        // if(deviceWidth > 1280) deviceWidth = 1280
        document.documentElement.style.fontSize = deviceWidth / 12.8 + 'px'
    },0)
    window.addEventListener('resize', function () {
        setTimeout(function () {
            let deviceWidth = document.documentElement.clientWidth
            // if(deviceWidth > 1280) deviceWidth = 1280
            document.documentElement.style.fontSize = deviceWidth / 12.8 + 'px'
        },0)
    })

    let renderLeft = name=>{
            let $dom = $(`.route_list dd[data-info="${name}"]`)
            let $pDom = $dom.closest('li')
            if(!$dom.hasClass('layui-this')) {
                $('.route_list .layui-this').removeClass('layui-this')
                $dom.addClass('layui-this')
            }
            if(!$pDom.hasClass('layui-nav-itemed')) {
                $('.route_list .layui-nav-itemed').removeClass('layui-nav-itemed')
                $pDom.addClass('layui-nav-itemed')
            }
        }
    window.addEventListener('popstate', function (e) {
        let path = location.pathname.substr(6)
        if(path === 'login') {
            console.log('login')
            // $('.route_list .layui-this').trigger('click')
            Route.replace(path)
            return
        }else if(path !== ''){
            renderLeft(path)
            Route.replace(path)
        }

    })
})()

let index = null
/*请求全局方法注册*/
layui.use('layer', ()=>{
    let layer = layui.layer
    $(document).ajaxSend(()=>{
        if(!index) {
            index = layer.load(1, {
                shade: [0.1,'#fff'] //0.1透明度的白色背景
            })
        }
    })

    $(document).ajaxSuccess((event, xhr, opt)=>{
        if(!(xhr.status === 200 || xhr.status === 206 || xhr.status === 304)){
            layer.msg('未知错误，请刷新重试!')
        }
        layer.close(index)
        index = null
    })

    $(document).ajaxError((event, xhr, opt)=>{
        let msg = ''
        switch (xhr.status) {
            case 500:
                msg = '服务器内部错误，请联系管理员！'
                break
            default:
                msg = '未知错误，请刷新重试!'
                break
        }
        layer.msg(msg)
        layer.close(index)
        index = null
    })
})

/*路由初始化*/
Route.init([
    {
        title: '登录页面',
        name: 'login',
        tpl: tpl.login,
        script: script.login
    },
    {
        title: '首页',
        name: 'exam',
        tpl: tpl.exam,
        script: script.exam
    },
    {
        title: '考试信息',
        name: 'examMsg',
        tpl: tpl.examMsg,
        script: script.examMsg
    },
    {
        title: '通知信息',
        name: 'inform',
        tpl: tpl.inform,
        script: script.inform
    },
    {
        title: '学校管理',
        name: 'school',
        tpl: tpl.school,
        script: script.school
    },
    {
        title: '个人设置',
        name: 'setting',
        tpl: tpl.setting,
        script: script.setting
    },
    {
        title: '区教研员管理',
        name: 'researcher',
        tpl: tpl.researcher,
        script: script.researcher
    }

])

let router = Util.getRouter('router')
/*初始化登录页面*/

modBase.$getS('AdminMsg') ? Route.push('login', {router}) : Route.push('login')
// router ? Route.push('login', {router}): Route.push('login')




