import $ from 'jquery'
import layui from 'layui'
import E from 'wangeditor'
import Base from 'src/scripts/common/base'
import modBase from "src/scripts/common/moduleBase"
import '../../styles/inform.scss'

let layer, laytpl, laydate, laypage, upload, editor, adminMsg, fileS = []
export default Object.assign({
    api: {
        CreateMsg: {
            type: 'POST',
            url: '/api/Message/CreateMsg'
        },
        GetJKYMsgs: {
            type: 'POST',
            url: '/api/Message/GetJKYMsgs'
        },
        UploadFile: {
            type: 'POST',
            url: '/api/Message/UploadFile'
        },
        GetSchMsgs: {
            type: 'POST',
            url: '/api/Message/GetSchMsgs'
        },
        ReadMsg: {
            type: 'POST',
            url: '/api/Message/ReadMsg'
        }
    },
    config: [
        {
            name: 'GetJKYMsgs',
            ele: '#inform1',
            view: '#inform'
        },
        {
            name: 'GetSchMsgs',
            ele: '#inform1',
            view: '#inform'
        }
    ],
    // 初始化
    init (data) {
        let me = this
        // adminMsg = modBase.$get('AdminMsg')
        adminMsg = modBase.$getS('AdminMsg')
        layui.use(['laytpl', 'layer', 'laydate', 'laypage', 'upload'], function () {
            layer = layui.layer, laytpl = layui.laytpl, laydate = layui.laydate, laypage = layui.laypage, upload = layui.upload
            let _data
            if (adminMsg.Type === 1) {  // 老师前端获取通知
                _data = {
                    SchoolID: adminMsg.ID,
                    Limit: 50,
                    Skip: 0
                }
                me.renderPage(1, _data)
            } else {
                _data = {
                    Limit: 50,
                    Skip: 0
                }
                me.renderPage(0, _data)
            }
            me.bindEvt()
        })
    },

    // 渲染页面
    renderPage (n, data) {
        let getTpl = $(`${ this.config[n].ele }`).html(), $view = $(`${ this.config[n].view }`), me = this
        modBase.$api(me.api[me.config[n].name], data)
            .then(res => {
                console.log(res)
                res.Data && modBase.$set('informData', res.Data)
                laytpl(getTpl).render({
                    list: res.Data ? res.Data : [],
                    type: adminMsg.Type
                }, function (dom) {
                    $view.html(dom)
                })
                // 初始化分页
                laypage.render({
                    elem: 'pageBottom'
                    ,count: res.Count
                    ,layout: ['count', 'limit', 'prev', 'page', 'next', 'skip']
                    ,limit: data.Limit
                    ,curr: (data.Skip / data.Limit) + 1
                    ,limits: [50, 100, 200]
                    ,jump: function(obj, first){
                        // console.log(obj)
                        if (!first) {
                            me.renderPage(n, {
                                Limit: obj.limit,
                                Skip: (obj.curr - 1) * obj.limit
                            })
                        }
                    }
                })
            })
            .catch(err => {me.$handleErr(err)})
    },

    // 事件绑定
    bindEvt () {
        let me = this
        // 返回
        $('#inform').on('click', '.goBack', function () {
            me.$route.push('inform')
        })
        // 发通知按钮
        $('#inform').on('click', '.notice', function () {
            me.createInform()
        })
        // 查看通知
        $('#inform').on('click', 'tr td a', function () {
            let id = $(this).parents('tr').data('id')
            me.lookInform(id)
            if ($(this).hasClass('on')) {
                modBase.$api(me.api['ReadMsg'], {
                    ID: id
                })
            }
        })
        // 发通知
        $('#inform').on('click', '.submitMsg', function () {
            console.log('发通知')
            me.submitMsg()
        })
        // 预览通知
        $('#inform').on('click', '.previewMsg', function () {
            console.log('预览通知')
            me.previewMsg()
        })
        // 删除附件
        $('#inform').on('click', '.fileLi em', function () {
            let me = this
            modBase.$layMsg('是否删除附件？', function () {
                fileS.splice($(me).parent().index(), 1)
                $(me).parent().remove()
                console.log(fileS)
            })
        })
        /*// 下载附件
        $('#inform').on('click', '.adjunct a', function () {
            window.open('http://' + $(this).data('info'), '_blank')
        })*/
    },

    // 创建通知
    createInform () {
        let getTpl = $('#inform2').html(), $view = $('#inform')
        laytpl(getTpl).render({}, function (dom) {
            $view.html(dom)
            //初始化上传控件
            upload.render({
                elem: '#upBtn'
                ,url: '/api/Message/UploadFile'
                ,accept: 'file' //普通文件
                ,done: function(res){
                    if (res.Success) {
                        layer.msg('附件上传成功！')
                        let h = `<li class="fileLi">${res.Data.split(',')[0]}<em></em></li>`
                        $('.fileList').append(h)
                        fileS.push({
                            nm: res.Data.split(',')[0],
                            url: res.Data.split(',')[1]
                        })
                    }
                    console.log(fileS)
                }
            });
            // 初始化富文本编辑器wangeditor
            editor = new E('.eCont')
            // 自定义菜单配置
            editor.customConfig.menus = [
                'head',  // 标题
                'bold',  // 粗体
                'fontSize',  // 字号
                'fontName',  // 字体
                'italic',  // 斜体
                'underline',  // 下划线
                'strikeThrough',  // 删除线
                'foreColor',  // 文字颜色
                'backColor',  // 背景颜色
                'link',  // 插入链接
                'list',  // 列表
                'justify',  // 对齐方式
                // 'quote',  // 引用
                'emoticon',  // 表情
                'image',  // 插入图片
                // 'table',  // 表格
                // 'video',  // 插入视频
                // 'code',  // 插入代码
                'undo',  // 撤销
                'redo'  // 重复
            ]
            editor.create()
            editor.txt.clear()
        })
    },

    // 查看通知
    lookInform (id) {
        let getTpl = $('#inform3').html(), $view = $('#inform'),  _info, data = modBase.$get('informData')
        data.forEach(function (item, index) {
            if (item.ID === id) {
                _info = item
            }
        })
        laytpl(getTpl).render(_info, function (dom) {
            $view.html(dom)
        })
    },

    // 发通知
    submitMsg () {
        let me = this, Title, PublishID, PublishName, Time, Content, data
        Title = $('input[name="Title"]')[0].value
        PublishID = adminMsg.ID
        PublishName = adminMsg.PublishName
        Time = modBase.$getNowFormatDate()
        Content = editor.txt.html()
        if (Title === '') {
            layer.msg('标题不能为空！')
        } else if (Content === '<p><br></p>') {
            layer.msg('内容不能为空！')
        } else {
            data = {
                Title: Title,
                PublishID: PublishID,
                PublishName: PublishName,
                Time: Time,
                Content: Content,
                Urls: fileS
            }
            modBase.$layMsg('是否确定发送通知？', function () {
                modBase.$api(me.api['CreateMsg'], data)
                    .then(res => {
                        layer.msg('发送成功！')
                        fileS = []  // 发送成功后，清空附件，避免再次发送时，数据重复
                        me.$route.push('inform')
                    })
                    .catch(err => {me.$handleErr(err)})
            })
        }
    },

    // 预览通知
    previewMsg () {
        let me = this, Title, PublishID, PublishName, Time, Content, data, tpl = $('#inform3').html()
        Title = $('input[name="Title"]')[0].value
        PublishID = adminMsg.ID
        PublishName = adminMsg.PersonName
        Time = modBase.$getNowFormatDate()
        Content = editor.txt.html()
        data = {
            Title: Title,
            PublishID: PublishID,
            PublishName: PublishName,
            Time: Time,
            Content: Content,
            Urls: fileS
        }
        laytpl(tpl).render(data, function (dom) {
            let dialog = layer.open({
                title: '预览通知',
                type: 1,
                area: ['100%','100%'],
                content: dom,
                success: function () {
                    $('.ui-top-cont').hide()
                    $(':focus').blur()
                }
            })
        })
    }
},Base)


