import $ from 'jquery'
import layui from 'layui'
import Base from 'src/scripts/common/base'
import '../../styles/exam.scss'
import modBase from  'src/scripts/common/moduleBase'
import examMsg from "src/scripts/exam/examMsg";

let layer, laytpl, laydate, form, adminMsg, examID
export default Object.assign({
    api: {
        GetExam: {
            type: 'POST',
            url: '/api/Exam/GetExam'
        },
        AddExam: {
            type: 'POST',
            url: '/api/Exam/AddExam'
        },
        DelExam: {
            type: 'POST',
            url: '/api/Exam/DelExam'
        },
        EditExam: {
            type: 'POST',
            url: '/api/Exam/EditExam'
        },
        ClearStudent: {
            type: 'POST',
            url: '/api/Student/ClearStudent'
        },
        ClearTeacher: {
            type: 'POST',
            url: '/api/Teacher/ClearTeacher'
        }
    },
    tpl: {
        0: '#examTpl',
        1: '#examTpl_1',
        2: '#examTpl_1'
    },
    init (data) {
        let me = this
        // adminMsg = modBase.$get('AdminMsg')
        adminMsg = modBase.$getS('AdminMsg')
        layui.use(['laytpl', 'layer', 'laydate', 'form'], function () {
            layer = layui.layer, laytpl = layui.laytpl, laydate = layui.laydate, form = layui.form
            me.renderPage()
            me.bindEvt()
        })
    },

    // 渲染页面
    renderPage () {
        // 获取所有考试
        modBase.$api(this.api.GetExam, {})
            .then(
                res=>{
                    let getTpl = $(this.tpl[adminMsg.Type]).html(), $view = $('#view')
                    laytpl(getTpl).render({
                        list: res,
                    }, function (dom) {
                        $view.html(dom)
                        $('.sp2').each(function (index, item) {
                            $(item).find('font').html(modBase.$timestampToTime(parseInt($(item).find('font').html())))
                        })
                        $('.new') && $('.new p i').each(function (index, item) {
                            $(item).html(modBase.$timestampToTime(parseInt($(item).html())))
                        })
                    })
                }
            ).catch(
            err=>{this.$handleErr(err)}
        )
    },

    bindEvt () {
        let me = this
        $('#exam').off('click')
        $('body').off('click')
        // 新增考试
        $('#exam').on('click', '#createExam', function() {
            let f = true
            $('#view li.li').each(function (index, item) {
                let state = $(item).data('info').ButtonState
                if(state === 0) {
                    layer.msg('已存在进行中的考试！')
                    f = false
                    return f
                }
            })
            f && me.createExam()
        })
        // 查看考试信息
        $('#exam').on('click', 'ul li.li', function (e) {
            if (e.target.id !== 'editExam' && e.target.id !== 'deleExam') {
                let data = $(this).data('info')
                me.viewExamInfo(data)
            }
        })
        // 修改考试信息
        $('#exam').on('click', '#editExam', function () {
            let data = $(this).parents('li').data('info')
            examID = data.ID
            me.editExamInfo(data)
        })
        // 删除考试信息
        $('#exam').on('click', '#deleExam', function () {
            let id = $(this).parents('li').data('info').ID
            modBase.$layMsg('是否删除考试？', function() {
                let data = {
                    ID: id
                }
                modBase.$api(me.api.DelExam, data)
                    .then(
                        res=>{
                            layer.msg('删除成功！')
                            me.renderPage()
                        }
                    )
                    .catch(
                        err=>{me.$handleErr(err)}
                    )
            })
        })
        // 新增科目点击事件
        $('body').on('click', '#addSubject,#addSubject_', function () {
            me.addSubject(this)
        })
        // 新增考生类型
        $('body').on('click', '#addType,#addType_', function () {
            me.addType(this)
        })
        // 删除科目/考生类型
        $('body').on('click', '.del', function () {
            $(this).parent().remove()
        })
        // 清空数据(考生信息)
        $('body').on('click', '#delBtn1', function () {
            if (!$(this).hasClass('disabled')) {
                modBase.$layMsg('是否确定清空数据？', function () {
                    modBase.$api(me.api['ClearStudent'], {
                        ID: examID
                    })
                        .then(res => {
                            layer.msg('操作成功！')
                        })
                        .catch(err => {me.$handleErr(err)})
                })
            }
        })
        // 清空数据(评卷老师信息)
        $('body').on('click', '#delBtn2', function () {
            if (!$(this).hasClass('disabled')) {
                modBase.$layMsg('是否确定清空数据？', function () {
                    modBase.$api(me.api['ClearTeacher'], {
                        ID: examID
                    })
                        .then(res => {
                            layer.msg('操作成功！')
                        })
                        .catch(err => {me.$handleErr(err)})
                })
            }
        })
        // 点选科目事件
        $('body').on('click', '.select', function () {
            let $dom = $(this).prev().find('input')
            let v = $dom.val()
            let info = $dom.data('info')
            if (!$(this).hasClass('disabled')) {
                if ($(this).hasClass('on')) {
                    $(this).removeClass('on')
                    $dom.removeClass('on')
                } else {
                    $(this).addClass('on')
                    $dom.addClass('on')
                    if (v === '') {
                        $dom.val(info)
                    }
                }
            }
        })
    },

    // 新建考试
    createExam () {
        let me = this
        let htmlS = ``
        htmlS = $('#addExamTpl').html()
        let dialog = layer.open({
            title: '新建考试信息',
            area: '1000px',
            type: 1,
            btn: ['发布'],
            btnAlign: 'c',
            content: htmlS,
            success: function (layero, index) {
                /*layero.addClass('layui-form')
                layero.find('.layui-layer-btn0').attr('lay-filter', 'fromVerify').attr('lay-submit', '')
                form.render()*/
                $(':focus').blur()
                laydate.render({
                    elem: '#stuStartTime',
                    type: 'datetime',
                    format: 'yyyy-MM-dd HH:mm:ss'
                })
                laydate.render({
                    elem: '#stuEndTime',
                    type: 'datetime',
                    format: 'yyyy-MM-dd HH:mm:ss'
                })
                laydate.render({
                    elem: '#tchStartTime',
                    type: 'datetime',
                    format: 'yyyy-MM-dd HH:mm:ss'
                })
                laydate.render({
                    elem: '#tchEndTime',
                    type: 'datetime',
                    format: 'yyyy-MM-dd HH:mm:ss'
                })
            },
            yes: function(index, layero){
                /*form.verify({
                    title: [ /[a-zA-Z\u4E00-\u9FA5]{1,20}$/, '不超过20个字'],
                    title1: function (value) {
                        if (value.length<5) {
                            return '不能超过6个字'
                        }
                    }
                })
                form.on('submit(fromVerify)', function (data) {
                    layer.alert(JSON.stringify(data.field), {
                        title: '最终提交信息'
                    })
                    return false
                })*/
                let _PublishID, _ExamName,_StuStartTime,_StuEndTime,_TchStartTime,_TchEndTime,_subNames = {},_stuTypes = '',data
                _PublishID = adminMsg.ID
                _ExamName = $('#examName')[0].value
                _StuStartTime = modBase.$timestampFormatDate($('#stuStartTime')[0].value)
                _StuEndTime = modBase.$timestampFormatDate($('#stuEndTime')[0].value)
                _TchStartTime = modBase.$timestampFormatDate($('#tchStartTime')[0].value)
                _TchEndTime = modBase.$timestampFormatDate($('#tchEndTime')[0].value)
                $('.subName.on').each(function (index, item) {
                    if ($(item).val() === '') {
                        $(item).val($(item).data('info'))
                    }
                    _subNames[$(item).data('key')] = $(item).val()
                })
                $('.stuType').each(function (index, item) {
                    if(item.value!==''){
                        _stuTypes += item.value + ','
                    }
                })
                _stuTypes = _stuTypes.substr(0,_stuTypes.length-1)
                if (_ExamName === '') {
                    layer.msg('考试名称不能为空！')
                } else if (_StuStartTime === '') {
                    layer.msg('考生信息录入起始时间不能为空！')
                } else if (_StuEndTime === '') {
                    layer.msg('考生信息录入截止时间不能为空！')
                } else if (_StuStartTime > _StuEndTime) {
                    layer.msg('考生信息录入起始时间不能大于截止时间！')
                } else if (_TchStartTime === '') {
                    layer.msg('评卷教师信息录入起始时间不能为空！')
                } else if (_TchEndTime === '') {
                    layer.msg('评卷教师信息录入截止时间不能为空！')
                } else if (_TchStartTime > _TchEndTime) {
                    layer.msg('评卷教师信息录入起始时间不能大于截止时间！')
                }  else if (JSON.stringify(_subNames) === '{}') {
                    layer.msg('科目设置不能为空！')
                } else if (_stuTypes === '') {
                    layer.msg('考生类型不能为空！')
                } else {
                    data = {
                        PublishID: _PublishID,
                        ExamName: _ExamName,
                        StuStartTime: _StuStartTime,
                        StuEndTime: _StuEndTime,
                        TchStartTime: _TchStartTime,
                        TchEndTime: _TchEndTime,
                        subNames: _subNames,
                        stuTypes: _stuTypes
                    }
                    modBase.$layMsg('是否发布？', function () {
                        modBase.$api(me.api.AddExam,data)
                            .then(
                                res=>{
                                    me.renderPage()
                                    layer.close(dialog)
                                    layer.msg('新增成功！')
                                }
                            ).catch(
                            err=>{me.$handleErr(err)}
                        )
                    })
                }
            }
        })
    },

    // 新增科目
    addSubject (e) {
        let addHtml = `
            <div class="ui-inline-cont">
                <div class="ui-input-inline">
                    <input type="tel" name="phone" lay-verify="title1" autocomplete="off" class="layui-input subName" placeholder="不超过5个字"   maxlength="5">
                </div>
                <button class="layui-btn layui-btn-primary ui-button del"><i class="layui-icon"></i></button>
            </div>
        `
        $(e).before(addHtml)
    },

    // 新增考生类型
    addType (e) {
        let addHtml = `
            <div class="ui-input-inline1">
                <input type="tel" name="stuType" lay-verify="title1" autocomplete="off" class="ui-input stuType" placeholder="不超过10个字"   maxlength="10">
                <a class="ui-inline-btn-del del"></a>
            </div>
        `
        $(e).before(addHtml)
    },

    // 查看考试信息
    viewExamInfo (param) {
        // console.log(param)
        Base.$set('examMsg', param)
        this.$route.push('examMsg')
    },

    // 修改考试信息
    editExamInfo (param) {
        console.log(param)
        let me = this
        let getTpl = $('#editExamTpl').html()
        let array1 = param.subNames
        let array2 = me.strToArray(param.stuTypes)
        laytpl(getTpl).render({
            ExamName: param.ExamName,
            StuStartTime: modBase.$timestampToTime(param.StuStartTime),
            StuEndTime: modBase.$timestampToTime(param.StuEndTime),
            TchStartTime: modBase.$timestampToTime(param.TchStartTime),
            TchEndTime: modBase.$timestampToTime(param.TchEndTime),
            subNames: array1,
            stuTypes: array2,
            IsScan: param.IsScan,
            StuStart: param.StuStart,
            TchStart: param.TchStart,
            StuEnd: param.StuEnd,
            TchEnd: param.TchEnd
        }, function (dom) {
            let dialog = layer.open({
                title: '修改考试信息',
                area: '1000px',
                type: 1,
                btn: ['保存'],
                btnAlign: 'c',
                content: dom,
                success: function () {
                    $(':focus').blur()
                    laydate.render({
                        elem: '#stuStartTime_',
                        type: 'datetime',
                        format: 'yyyy-MM-dd HH:mm:ss',
                        done: function (value, date, endDate) {
                            console.log('123')
                            console.log(value)
                            console.log(date)
                            console.log(endDate)

                        }
                    })
                    laydate.render({
                        elem: '#stuEndTime_',
                        type: 'datetime',
                        format: 'yyyy-MM-dd HH:mm:ss',
                        change: function (value, date, endDate) {
                            console.log('123')
                            console.log(value)
                            console.log(date)
                            console.log(endDate)

                        }
                    })
                    laydate.render({
                        elem: '#tchStartTime_',
                        type: 'datetime',
                        format: 'yyyy-MM-dd HH:mm:ss'
                    })
                    laydate.render({
                        elem: '#tchEndTime_',
                        type: 'datetime',
                        format: 'yyyy-MM-dd HH:mm:ss'
                    })
                },
                yes: function(index, layero){
                    let _ExamName,_StuStartTime,_StuEndTime,_TchStartTime,_TchEndTime,_subNames = {},_stuTypes = '',data
                    _ExamName = $('#examName_')[0].value
                    _StuStartTime = modBase.$timestampFormatDate($('#stuStartTime_')[0].value)
                    _StuEndTime = modBase.$timestampFormatDate($('#stuEndTime_')[0].value)
                    _TchStartTime = modBase.$timestampFormatDate($('#tchStartTime_')[0].value)
                    _TchEndTime = modBase.$timestampFormatDate($('#tchEndTime_')[0].value)
                    $('.subName.on').each(function (index, item) {
                        if ($(item).val() === '') {
                            $(item).val($(item).data('info'))
                        }
                        _subNames[$(item).data('key')] = $(item).val()
                    })
                    $('.stuType').each(function (index, item) {
                        if(item.value!==''){
                            _stuTypes += item.value + ','
                        }
                    })
                    _stuTypes = _stuTypes.substr(0,_stuTypes.length-1)
                    if (_ExamName === '') {
                        layer.msg('考试名称不能为空！')
                    } else if (_StuStartTime === '') {
                        layer.msg('考生信息录入起始时间不能为空！')
                    } else if (_StuEndTime === '') {
                        layer.msg('考生信息录入截止时间不能为空！')
                    } /*else if ( _StuEndTime - _StuStartTime > 86400000) {
                        layer.msg('考生信息录入截止时间必须晚于录入时间24小时！')
                    } */else if (_TchStartTime === '') {
                        layer.msg('评卷教师信息录入起始时间不能为空！')
                    } else if (_TchEndTime === '') {
                        layer.msg('评卷教师信息录入截止时间不能为空！')
                    } /*else if ( _TchEndTime - _TchStartTime > 86400000) {
                        layer.msg('评卷教师信息录入截止时间必须晚于录入时间24小时！')
                    } */else if (JSON.stringify(_subNames) === '{}') {
                        layer.msg('科目设置不能为空！')
                    } else if (_stuTypes === '') {
                        layer.msg('考生类型不能为空！')
                    } else {
                        data = {
                            ID: param.ID,
                            ExamName: _ExamName,
                            StuStartTime: _StuStartTime,
                            StuEndTime: _StuEndTime,
                            TchStartTime: _TchStartTime,
                            TchEndTime: _TchEndTime,
                            subNames: _subNames,
                            stuTypes: _stuTypes
                        }
                        modBase.$layMsg('是否保存？', function () {
                            modBase.$api(me.api.EditExam, data)
                                .then(
                                    res=>{
                                        me.renderPage()
                                        layer.close(dialog)
                                        layer.msg('编辑成功！')
                                    }
                                ).catch(
                                err=>{this.$handleErr(err)}
                            )
                        })
                    }
                }
            })
        })

    },

    // 截取字符串放回数组
    strToArray (str) {
        let array = []
        array = str.split(',')
        return array
    }
},Base)


