import $ from 'jquery'


export default {
    getUrlQuery (name) {
        if(location.search) {
            let uri = location.search.substr(1)
            let reg = new RegExp(`(?:^|&)${name}=([^&]+)(?:&|$)`, 'i')
            let res = uri.match(reg)
            return res && res[1]
        }
    },

    getRouter (name) {
        if(location.search) {
            let uri = location.search.substr(1)
            let reg = new RegExp(`(?:^|&)${name}=([^&]+)(?:&|$)`, 'i')
            let res = uri.match(reg)
            return res && res[1]
        }else {
            if(/\./.test(location.pathname.substr(6))) {
                return ''
            }
            return location.pathname.substr(6) + location.search
        }
    },

    request (obj) {
        $.ajax(obj)
    },
    _data: {

    },
    set (key, val) {
        this._data[key] = val
    },
    get (key) {
        return typeof this._data[key] === 'undefined'? null:this._data[key]
    },
    clear (key) {
        delete this._data[key]
    },
    clearAll () {
        this._data = {}
    },
    valid (rules, data, cb) {
        let flag = true
        for (let item in rules) {
            if(!data[item] && typeof rules[item] !== 'object'){
                cb(rules[item])
                flag=false
                break
            }else if(data[item] && typeof rules[item] === 'object' ){
                if(Object.prototype.toString.call(rules[item][0]) === "[object RegExp]"){
                    if(!rules[item][0].test(data[item])) {
                        cb(rules[item][1])
                        flag = false
                        break
                    }
                }
            }
        }
        return flag
    }
}


