import $ from 'jquery'
import layui from 'layui'
import Base from 'src/scripts/common/base'
import exam from "src/scripts/exam/exam";
import moduleBase from "src/scripts/common/moduleBase";

let layer
export default Object.assign({
    getUserInfo () {
        return new Promise((resolve, reject) => {
            this.$request({
                type: 'GET',
                url: '/api/oms/user',
                dataType: 'json',
                success: function (res) {
                    if(res.Success) {
                        resolve(res.Data)
                    }else {
                        reject(res)
                    }
                },
                error: function (err) {
                    reject(err)
                }
            })
        })
    },

    // 登录接口
    loginRequest () {
        return new Promise((resolve, reject) => {
            this.$request({
                type: 'POST',
                url: '/api/Admin/Login',
                data: JSON.stringify({
                    account: $('.login input[name="userName"]').val(),
                    password: $('.login input[name="pwd"]').val()
                }),
                contentType: 'application/json',
                dataType: 'json',
                success: function (res) {
                    if(res.Success) {
                        resolve(res.Data)
                    }else {
                        reject(res)
                    }
                },
                error: function (err) {
                    reject(err)
                }
            })
        })
    },

    logoutRequest (account) {
        return new Promise((resolve, reject) => {
            this.$request({
                type: 'POST',
                url: '/api/loginOut',
                data: JSON.stringify({
                    account: account
                }),
                contentType: 'application/json',
                dataType: 'json',
                success: function (res) {
                    if(res.Success) {
                        resolve(res.Data)
                    }else {
                        reject(res)
                    }
                },
                error: function (err) {
                    reject(err)
                }
            })
        })
    },

    // 给顶部菜单栏绑定事件
    routeEvt () {
        let me = this
        $('body .menuUl').on('click', 'li', function () {
            me.$route.push($(this).data('info'))
            $(this).siblings().removeClass('ui-this').end().addClass('ui-this')
            layer.closeAll()
        })
    },

    renderLeft (name) {
        let $dom = $(`.route_list dd[data-info="${name}"]`)
        let $pDom = $dom.closest('li')
        if(!$dom.hasClass('layui-this')) {
            $('.route_list .layui-this').removeClass('layui-this')
            $dom.addClass('layui-this')
        }
        if(!$pDom.hasClass('layui-nav-itemed')) {
            $('.route_list .layui-nav-itemed').removeClass('layui-nav-itemed')
            $pDom.addClass('layui-nav-itemed')
        }
    },

    renderTop (name) {
        if(name ===  'examMsg'){
            name = 'exam'
        }
        let $dom = $(`.menuUl li[data-info="${name}"]`)
        if(!$dom.hasClass('ui-this')) {
            $('.menuUl li').removeClass('ui-this')
            $dom.addClass('ui-this')
        }
    },

    loginAfter (res) {
        let me = this
        //moduleBase.$set('AdminMsg', res)
        moduleBase.$setS('AdminMsg', res)
        this.$route.renderFrame(function () {
            let $dom = $('#userInfo')
            // 更新用户名
            $dom.html(res.PersonName).data('info', JSON.stringify(res))
            // 绑定注销事件
            $('#logout').on('click', function () {
                // location.reload()
                // 清空登录数据
                moduleBase.$clearS('AdminMsg')
                me.$route.push('login')
                layer.closeAll()
            })
            $('.actDiv').on('mouseover mouseout', 'span', function () {
                $(this).find('#logout').stop().slideToggle()
            })
            // 用户类型为1，2 分别代表学校，教研员
            if (res.Type === 1 || res.Type === 2) {
                $('.menuUl .li3,.menuUl .li5').hide()   // 隐藏学校管理，教研员管理
                $('.header .logo span').html('深圳市教科院考试报名系统前台')
                if(res.Type === 1) {
                    $('.header .top_H p').html(res.SchoolName)
                } else if (res.Type === 2) {
                    $('.header .top_H p').html(res.Area)
                }
            } else {
                $('.header .top_H').hide()
            }

            // 登录成功默认跳转至exam
            if(me.router !== 'login') {
                let pRouter = me.router || 'exam'
                me.$route.push(pRouter)
                if(pRouter !== 'exam') {
                    // this.renderLeft(pRouter)
                    me.renderTop(pRouter)
                }
            }else {
                me.$route.push('exam')
            }
        })
        this.routeEvt()
    },

    bindEvt () {
        let me = this
        $('body #login').on('click', function () {
            me.loginRequest()
                .then(res => {
                    /*登录后的界面*/
                    me.loginAfter(res)
                }, err => {
                    layer.msg(err.Message)
                })
        })

        $('body .login input[name="pwd"]').on('keyup', function (e) {
            if (e.keyCode ===13) {
                me.loginRequest()
                    .then(res => {
                        /*登录后的界面*/
                        me.loginAfter(res)
                    }, err => {
                        layer.msg(err.Message)
                    })
            }
        })
    },

    init (data) {
        data && (this.router = data.router)
        this.bindEvt()
        layui.use(['form', 'layer'], ()=>{
            layer = layui.layer
            //todo 判断用户登录状态？
            let res = moduleBase.$getS('AdminMsg')
            if (res) {
                this.loginAfter(res)
            }
            // this.loginRequest().then(res=>{
            //     /!*登录后的界面*!/
            //     console.log(res)
            //     this.loginAfter(res)
            // }).catch(err=>{
            //     // layer.msg(err.Message || '请登录')
            // })
        })
    }
}, Base)


