import $ from 'jquery'
import layui from 'layui'
import Base from 'src/scripts/common/base'
import '../../styles/school.scss'
import modBase from "src/scripts/common/moduleBase"

let layer, laytpl, laydate, element, upload, form, adminMsg, laypage
export default Object.assign({
    api: {
        GetAllSchool: {
            type: 'POST',
            url: '/api/Score/GetAllSchool'
        },
        SignSchool: {
            type: 'POST',
            url: '/api/Admin/SignSchool'
        },
        EditSchoolUser: {
            type: 'POST',
            url: '/api/Admin/EditSchoolUser'
        },
        DelUser: {
            type: 'POST',
            url: '/api/Admin/DelUser'
        },
        ExportSchool: {
            type: 'POST',
            url: '/api/Admin/ExportSchool'
        },
        ReSetPwd: {
            type: 'POST',
            url: '/api/Admin/ReSetPwd'
        }
    },
    // 初始化
    init (data) {
        let me = this
        // adminMsg = modBase.$get('AdminMsg')
        adminMsg = modBase.$getS('AdminMsg')
        layui.use(['laytpl', 'layer', 'laydate', 'element', 'form', 'upload', 'laypage'], function () {
            layer = layui.layer, laytpl = layui.laytpl, laydate = layui.laydate, element = layui.element, form = layui.form, upload = layui.upload, laypage = layui.laypage
            me.renderPage({
                Limit: 50,
                Skip: 0
            })
            me.bindEvt()
        })
    },

    // 渲染页面
    renderPage (data) {
        let me = this, getTpl = $('#schoolTpl').html(), $view = $('#view')
        modBase.$api(me.api['GetAllSchool'], data)
            .then(res => {
                laytpl(getTpl).render({
                    list: res.Data
                }, function (dom) {
                    $view.html(dom)
                })
                // 初始化分页
                laypage.render({
                    elem: 'pageBottom'
                    ,count: res.Count
                    ,layout: ['count', 'limit', 'prev', 'page', 'next', 'skip']
                    ,limit: data.Limit
                    ,curr: (data.Skip / data.Limit) + 1
                    ,limits: [50, 100, 200]
                    ,jump: function(obj, first){
                        // console.log(obj)
                        if (!first) {
                            me.renderPage({
                                Limit: obj.limit,
                                Skip: (obj.curr - 1) * obj.limit
                            })
                        }
                    }
                })
            })
            .catch(err => {me.$handleErr(err)})
    },

    // 事件绑定
    bindEvt () {
        let me = this
        // 新增学校
        $('#school').on('click', '.createSchool', function () {
            me.addSchool()
        })
        // 编辑学校
        $('#school').on('click', '.editBtn', function () {
            let data = $(this).parents('tr').data('info')
            me.editSchool(data)
        })
        // 重置密码
        $('#school').on('click', '.resetPwdBtn', function () {
            let Account = $(this).parents('tr').data('info').ID
            me.reSetPwd(Account)
        })
        // 删除学校
        $('#school').on('click', '.deleBtn', function () {
            let id = $(this).parents('tr').data('info').ID
            let data = {
                ID: id
            }
            modBase.$layMsg('是否删除学校？', function () {
                me.delSchool(data)
            })
        })
        // 公共按钮事件
        $('#school').on('click', '.actLi', function () {
            let index = $(this).index()
            switch (index) {
                case 0:
                    me.exportSchool()
                    break
                case 1:
                    console.log('打印当页')
                    break
            }
        })
        // 搜索学校
        $('#school').on('click', '.search', function () {
            let data = {
                School: $('#school input[name="StudentName1"]')[0].value,
                Limit: 50,
                Skip: 0
            }
            me.searchSchool(data)
        })
    },

    // 新增学校
    addSchool () {
        let me = this, dom = $('#schoolTpl1').html(), _AreaID
        let dialog = layer.open({
            title: '新增学校',
            area: '500px',
            type: 1,
            btn: ['确定', '取消'],
            content: dom,
            btnAlign: 'c',
            success: function (index, layero) {
                $(':focus').blur()
                _AreaID = '0'
                form.render('select')
                form.on('select(Area)', function (data) {
                    console.log(data.value)
                    _AreaID = data.value
                })
            },
            btn1: function (index, layero) {
                let ID, SchoolName, PersonName, Tel, Email, QQ, data, Area
                ID = $('.addSchoolMsg input[name="ID"]')[0].value
                SchoolName = $('.addSchoolMsg input[name="SchoolName"]')[0].value
                PersonName = $('.addSchoolMsg input[name="PersonName"]')[0].value
                Tel = $('.addSchoolMsg input[name="Tel"]')[0].value
                Email = $('.addSchoolMsg input[name="Email"]')[0].value
                QQ = $('.addSchoolMsg input[name="QQ"]')[0].value
                Area = _AreaID
                if (ID === '') {
                    layer.msg('学校代号不能为空！')
                } else if (SchoolName === '') {
                    layer.msg('学校简称不能为空！')
                } else if (Area === '0') {
                    layer.msg('所属区域不能为空！')
                } else if (PersonName === '') {
                    layer.msg('负责人姓名不能为空！')
                } else if (Tel === '') {
                    layer.msg('联系电话不能为空！')
                } else if (Email === '') {
                    layer.msg('邮箱不能为空！')
                } else {
                    data = {
                        ID: ID,
                        SchoolName: SchoolName,
                        AreaID: Area,
                        PersonName: PersonName,
                        Tel: Tel,
                        Email: Email,
                        QQ: QQ,
                    }
                    modBase.$layMsg('是否新增学校', function () {
                        modBase.$api(me.api['SignSchool'], data)
                            .then(res => {
                                layer.msg('新增成功')
                                layer.close(dialog)
                                me.renderPage({
                                    Limit: 50,
                                    Skip: 0
                                })
                            })
                            .catch(err => {me.$handleErr(err)})
                    })
                }
            }
        })
    },

    // 编辑学校
    editSchool (data) {
        let me = this, getTpl = $('#schoolTpl2').html(), _AreaID
        laytpl(getTpl).render(data, function (dom) {
            let dialog = layer.open({
                title: '修改学校信息',
                area: '500px',
                type: 1,
                btn: ['确定', '取消'],
                content: dom,
                btnAlign: 'c',
                success: function (index, layero) {
                    $(':focus').blur()
                    _AreaID = '0'
                    form.render('select')
                    form.on('select(Area1)', function (data) {
                        console.log(data.value)
                        _AreaID = data.value
                    })
                },
                btn1: function (index, layero) {
                    let ID, SchoolName, PersonName, Tel, Email, QQ, data, Area
                    ID = $('.editSchoolMsg input[name="ID"]')[0].value
                    SchoolName = $('.editSchoolMsg input[name="SchoolName"]')[0].value
                    PersonName = $('.editSchoolMsg input[name="PersonName"]')[0].value
                    Tel = $('.editSchoolMsg input[name="Tel"]')[0].value
                    Email = $('.editSchoolMsg input[name="Email"]')[0].value
                    QQ = $('.editSchoolMsg input[name="QQ"]')[0].value
                    Area = _AreaID
                    if (ID === '') {
                        layer.msg('学校代号不能为空！')
                    } else if (SchoolName === '') {
                        layer.msg('学校简称不能为空！')
                    } else if (Area === '0') {
                        layer.msg('所属区域不能为空！')
                    } else if (PersonName === '') {
                        layer.msg('负责人姓名不能为空！')
                    } else if (Tel === '') {
                        layer.msg('联系电话不能为空！')
                    } else if (Email === '') {
                        layer.msg('邮箱不能为空！')
                    } else {
                        data = {
                            ID: ID,
                            SchoolName: SchoolName,
                            AreaID: Area,
                            PersonName: PersonName,
                            Tel: Tel,
                            Email: Email,
                            QQ: QQ,
                        }
                        modBase.$api(me.api['EditSchoolUser'], data)
                            .then(res => {
                                layer.msg('修改成功')
                                layer.close(dialog)
                                me.renderPage({
                                    Limit: 50,
                                    Skip: 0
                                })
                            })
                            .catch(err => {me.$handleErr(err)})
                    }
                }
            })
        })
    },

    // 搜索学校
    searchSchool (data) {
        let me = this, getTpl = $('#schoolTpl').html(), $view = $('#view')
        me.renderPage(data)
    },

    // 删除学校
    delSchool (data) {
        let me = this
        modBase.$api(me.api['DelUser'], data)
            .then(res => {
                layer.msg('删除成功！')
                me.renderPage({
                    Limit: 50,
                    Skip: 0
                })
            })
            .catch(err => {me.$handleErr(err)})
    },

    // 数据导出
    exportSchool () {
        let me = this
        modBase.$api(me.api['ExportSchool'], {})
            .then(res => {
                window.location.href = 'http://' + res
            })
            .catch(err => {me.$handleErr(err)})
    },

    //  重置密码
    reSetPwd (acount) {
        let me = this,Account = acount
        modBase.$layMsg('是否确认重置密码？', function () {
            modBase.$api(me.api['ReSetPwd'], {Account: Account})
                .then(res => {
                    layer.msg('密码重置成功，重置密码均为666666')
                })
                .catch(err => {me.$handleErr(err)})
        })
    }
}, Base)


