import tpl from 'src/tpl'
import layui from 'layui'

/*渲染公共框架*/
let renderFrame = (cb) =>{
    document.getElementById('app').innerHTML =  `
        <div class="layui-layout layui-layout-admin">
            ${tpl.head}
            <div id="manageBody"></div>
        </div>
    `
    setTimeout(()=>{
        cb()
    })
}

let instantiateModule = (name, data) => {
    let R = Route.routeMap[name]
    R['title'] && (document.title = R['title'])
    switch (name) {
        case 'login':
            document.getElementById('app').innerHTML = R['tpl']
            R['script'].init(data)
            break
        /*case 'exam':
            document.getElementById('')
            // document.getElementById('left').style.display = 'none'
            // document.getElementById('foot').style.display = 'none'
            document.getElementById('manageBody').innerHTML = R['tpl']
            R['script'].init(data)
            break*/
        default:
            document.getElementById('manageBody').innerHTML = R['tpl']
            R['script'].init(data)
            break
    }
}



let Route = {
    replace: (name, data) => {
        window.history.replaceState(null, '', `/page/${name}`)
        instantiateModule(name.split('?')[0], data)
    },
    push: (name, data) => {
        window.history.pushState(null, '', `/page/${name}`)
        instantiateModule(name.split('?')[0], data)
    },
    routeMap: {},
    renderFrame: renderFrame,
    init: routeArr => {
        routeArr.forEach(item=>{
            Route.routeMap[item.name] = item
        })
    }
}

export default Route


