import $ from 'jquery'
import layui from 'layui'
import E from 'wangeditor'
import Base from 'src/scripts/common/base'
import modBase from "src/scripts/common/moduleBase"
import '../../styles/researcher.scss'

let layer, laytpl, laydate, laypage, upload, form, editor, adminMsg
export default Object.assign({
    api: {
        GetArea: {
            type: 'POST',
            url: '/api/Admin/GetArea'
        },
        SignArea: {
            type: 'POST',
            url: '/api/Admin/SignArea'
        },
        EditArea: {
            type: 'POST',
            url: '/api/Admin/EditArea'
        },
        ExportArea: {
            type: 'POST',
            url: '/api/Admin/ExportArea'
        },
        ReSetPwd: {
            type: 'POST',
            url: '/api/Admin/ReSetPwd'
        },
        DelUser: {
            type: 'POST',
            url: '/api/Admin/DelUser'
        }
    },
    // 初始化
    init (data) {
        let me = this
        adminMsg = modBase.$getS('AdminMsg')
        layui.use(['laytpl', 'layer', 'laydate', 'laypage', 'upload', 'form'], function () {
            layer = layui.layer, laytpl = layui.laytpl, laydate = layui.laydate, laypage = layui.laypage, upload = layui.upload,form = layui.form
            let _data = {
                Limit: 50,
                Skip: 0
            }
            me.renderPage(0, _data)
            me.bindEvt()
        })
    },

    // 渲染页面
    renderPage (n, data) {
        let getTpl = $('#researcherTpl').html(), $view = $('.view'), me = this
        modBase.$api(me.api['GetArea'], data)
            .then(res => {
                console.log(res)
                res.Data && modBase.$set('informData', res.Data)
                laytpl(getTpl).render({
                    list: res.Data ? res.Data : [],
                }, function (dom) {
                    $view.html(dom)
                })
                // 初始化分页
                laypage.render({
                    elem: 'pageBottom'
                    ,count: res.Count
                    ,layout: ['count', 'limit', 'prev', 'page', 'next', 'skip']
                    ,limit: data.Limit
                    ,curr: (data.Skip / data.Limit) + 1
                    ,limits: [50, 100, 200]
                    ,jump: function(obj, first){
                        // console.log(obj)
                        if (!first) {
                            me.renderPage(n, {
                                Limit: obj.limit,
                                Skip: (obj.curr - 1) * obj.limit
                            })
                        }
                    }
                })
            })
            .catch(err => {me.$handleErr(err)})
    },

    // 事件绑定
    bindEvt () {
        let me = this
        // 新增教研员
        $('#researcher').on('click', '.createResearcher', function () {
            console.log('新增教研员')
            me.signArea()
        })
        // 公共按钮事件
        $('#researcher').on('click', '.actLi', function () {
            let index = $(this).index()
            switch (index) {
                case 0:
                    console.log('数据导出')
                    me.exportArea()
                    break
                case 1:
                    console.log('打印当页')
                    break
            }
        })
        // 编辑教研员
        $('#researcher').on('click', '.editBtn', function () {
            console.log('编辑教研员')
            let data = $(this).parents('tr').data('info')
            me.editArea(data)
        })
        // 删除
        $('#researcher').on('click', '.deleBtn', function () {
            console.log('删除教研员')
            let id = $(this).parents('tr').data('info').ID
            me.delUser(id)
        })
        // 重置密码
        $('#researcher').on('click', '.resetPwdBtn', function () {
            console.log('重置密码')
            let id = $(this).parents('tr').data('info').ID
            me.reSetPwd(id)
        })
    },

    // 数据导出
    exportArea () {
        let me = this
        modBase.$api(me.api['ExportArea'], {})
            .then(res => {
                window.location.href = 'http://' + res
            })
            .catch(err => {me.$handleErr(err)})
    },

    // 新增区域教研员
    signArea () {
        let me = this, dom = $('#researcherTpl1').html(), _AreaID
        let dialog = layer.open({
            title: '新增教研员',
            area: '500px',
            type: 1,
            btn: ['确定', '取消'],
            content: dom,
            btnAlign: 'c',
            success: function (index, layero) {
                _AreaID = '0'
                form.render('select')
                form.on('select(Area)', function (data) {
                    console.log(data.value)
                    _AreaID = data.value
                })
                $(':focus').blur()
            },
            btn1: function (index, layero) {
                let ID, AreaID, Name, Tel, Email, QQ, data
                ID = $('.addResearcherMsg input[name="ID"]')[0].value
                AreaID = _AreaID
                Name = $('.addResearcherMsg input[name="Name"]')[0].value
                Tel = $('.addResearcherMsg input[name="Tel"]')[0].value
                Email = $('.addResearcherMsg input[name="Email"]')[0].value
                QQ = $('.addResearcherMsg input[name="QQ"]')[0].value
                if (ID === '') {
                    layer.msg('账号不能为空！')
                } else if (AreaID === '0') {
                    layer.msg('所属区域不能为空！')
                } else if (Name === '') {
                    layer.msg('教研员名称不能为空！')
                } else if (Tel === '') {
                    layer.msg('联系电话不能为空！')
                } else if (Email === '') {
                    layer.msg('邮箱不能为空！')
                } else {
                    data = {
                        ID: ID,
                        AreaID: AreaID,
                        Name: Name,
                        Tel: Tel,
                        Email: Email,
                        QQ: QQ,
                    }
                    modBase.$layMsg('是否新增区域管理员', function () {
                        modBase.$api(me.api['SignArea'], data)
                            .then(res => {
                                layer.msg('新增成功')
                                layer.close(dialog)
                                me.renderPage(0, {
                                    Limit: 50,
                                    Skip: 0
                                })
                            })
                            .catch(err => {me.$handleErr(err)})
                    })
                }
            }
        })
    },

    // 编辑教研员
    editArea (data) {
        let me = this, getTpl = $('#researcherTpl2').html(), _AreaID
        laytpl(getTpl).render(data, function (dom) {
            let dialog = layer.open({
                title: '编辑教研员',
                area: '500px',
                type: 1,
                btn: ['确定', '取消'],
                content: dom,
                btnAlign: 'c',
                success: function (index, layero) {
                    _AreaID = '0'
                    form.render('select')
                    form.on('select(Area1)', function (data) {
                        console.log(data.value)
                        _AreaID = data.value
                    })
                    $(':focus').blur()
                },
                btn1: function (index, layero) {
                    let ID, AreaID, Name, Tel, Email, QQ, data
                    ID = $('.editResearcherMsg input[name="ID"]')[0].value
                    AreaID = _AreaID
                    Name = $('.editResearcherMsg input[name="Name"]')[0].value
                    Tel = $('.editResearcherMsg input[name="Tel"]')[0].value
                    Email = $('.editResearcherMsg input[name="Email"]')[0].value
                    QQ = $('.editResearcherMsg input[name="QQ"]')[0].value
                    if (ID === '') {
                        layer.msg('账号不能为空！')
                    } else if (AreaID === '0') {
                        layer.msg('所属区域不能为空！')
                    } else if (Name === '') {
                        layer.msg('教研员名称不能为空！')
                    } else if (Tel === '') {
                        layer.msg('联系电话不能为空！')
                    } else if (Email === '') {
                        layer.msg('邮箱不能为空！')
                    } else {
                        data = {
                            ID: ID,
                            AreaID: AreaID,
                            PersonName: Name,
                            Tel: Tel,
                            Email: Email,
                            QQ: QQ,
                        }
                        modBase.$api(me.api['EditArea'], data)
                            .then(res => {
                                layer.msg('修改成功')
                                layer.close(dialog)
                                me.renderPage(0, {
                                    Limit: 50,
                                    Skip: 0
                                })
                            })
                            .catch(err => {me.$handleErr(err)})
                    }
                }
            })
        })
    },

    // 重置密码
    reSetPwd (id) {
        let me = this
        modBase.$layMsg('是否重置密码？', function () {
            modBase.$api(me.api['ReSetPwd'],{
                Account: id
            })
                .then(res => {
                    layer.msg('密码重置成功，重置密码均为666666')
                })
                .catch(err => {me.$handleErr(err)})
        })
    },

    // 删除教研员
    delUser (id) {
        let me = this
        modBase.$layMsg('是否删除教研员？', function () {
            modBase.$api(me.api['DelUser'],{
                ID: id
            })
                .then(res => {
                    layer.msg('操作成功！')
                    me.renderPage(0, {
                        Limit: 50,
                        Skip: 0
                    })
                })
                .catch(err => {me.$handleErr(err)})
        })
    }
},Base)


