import Route from 'src/scripts/route'
import layui from 'layui'

export default (err)=>{
    // 登录失效或未登录，请重新登录
    layui.use('layer', ()=> {
        if (typeof err.Message === 'string') {
            layer.msg(err.Message)
            if (err.Message === '未登录') {
                setTimeout(() => {
                    // Route.push('login')
                    document.title = '登录页面'
                    location.reload()
                }, 1000)
            }
        }
    })
}


